<template>
    <div>
        <navbar />
         <b-container>
            <h2>Campaigns</h2>
                <b-table striped hover :items="campaigns"  :fields="fields" dark>
                <template #cell(Action)="row">
                    <div v-if="row.item.status === 'Pending'">
                        <b-button size="sm" variant="success" :props="row" @click="updateCampaign(row.item._id, 'Approved')" class="m-2" >
                            Approve
                        </b-button>

                        <b-button size="sm" variant="danger" @click="updateCampaign(row.item._id, 'Declined')" class="m-2">
                            Decline
                        </b-button>
                    </div>
                    
                    <b-td v-if="row.item.status !== 'Pending'">
                        {{row.item.status}}
                    </b-td>

                </template>
            </b-table>
        </b-container>
    </div>
</template>

<script>
import { api } from '../helpers/helpers';
import VueJwtDecode from "vue-jwt-decode";
import axios from 'axios'
import Navbar from '../components/Navbar.vue';

export default {
  components: { Navbar },
    data(){
        return {
            campaigns: [],
            user: null,
            fields: ['Topic','Message','PostedBy','PostedDate', 'status', 'Action']
        }
    },
    methods: {
         getUserDetails() {
            let token = localStorage.getItem("jwt");
            let decoded = VueJwtDecode.decode(token);
            this.user = decoded;
        },
        updateCampaign(id, status){
            console.log(id, status)
            axios.post('http://localhost:3000/user/campaign/update', {id, status})
                .then(async response => {
                    console.log(response.data)
                    let res = await api.getAllCampaigns();
                    this.campaigns = res
                })
                .catch(err => {
                    alert('Error occured')
                })
        }
    },
    async mounted() {
        let res = await api.getAllCampaigns();
        this.campaigns = res
        this.getUserDetails();
    }

}
</script>

